import React, { Component } from 'react';
import Movie from './Movie'
import MovieInfo from './MovieInfo'
import './Movies.css'

const axios = require('axios').default;


async function getMovie(id) {
	var str1 = 'http://www.omdbapi.com/?i='
	var str2 = id;
	var str3 = '&apikey=287d3faa';
	var url = (str1.concat(str2)).concat(str3);

	let receive = axios.get(url);
	receive
	.then(response => {
		const data = response.data;
		return data["Title"];
	})
}


