import React, { Component } from 'react';
import './Movie.css'

export class Movie extends Component {
	constructor() {
		super();
		this.state = {
			clicked: 0
		}
		this.displayContent = () => {
			if(this.state.clicked == 0) {
				return (
				<div className='movie'>
					<figure className="tint">
						<img src={this.props.info.Poster} className="poster"/>
					</figure>
				</div>
				)
			}
		}
	}
	render() {
		return (
			<div onClick={this.props.setClicked}>
				{this.displayContent()}
			</div>
		);
	}
}
export default Movie;
